const ClubSchedule = () => {
  const [events, setEvents] = useState([]);
  const [selectedEvent, setSelectedEvent] = useState(null);
  const [modalIsOpen, setModalIsOpen] = useState(false);

  useEffect(() => {
    const mappedEvents = eventsData.map(event => ({
      ...event,
          participants: 0, // Initialize participants to 0
    }));
    setEvents(mappedEvents);
  }, []);

  // useEffect(() => {
  //   // Example of fetching the JSON data from a file or API
  //   fetch('/assets/events.json')
  //     .then(response => response.json())
  //     .then(data => {
  //       // Initialize participant count
  //       const initializedEvents = data.map(event => ({
  //         ...event,
  //         participants: 0, // Initialize participants to 0
  //       }));
  //       setEvents(initializedEvents);
  //     });
  // }, []);

  const handleEventClick = (info) => {
    const clickedEvent = events.find(event => event.id === info.event.id);
    setSelectedEvent(clickedEvent);
    setModalIsOpen(true);
  };

  const handleParticipate = () => {
    if (selectedEvent.participants < 20) {
      // Update the participants count for the selected event
      const updatedEvents = events.map(event =>
        event.id === selectedEvent.id
          ? { ...event, participants: event.participants + 1 }
          : event
      );
      setEvents(updatedEvents);
      setSelectedEvent({ ...selectedEvent, participants: selectedEvent.participants + 1 });
      alert('You have successfully registered for the event!');
    } else {
      alert('Sorry, this event is full.');
    }
    setModalIsOpen(false);
  };

  return (
    <div className="app-container">
      <Header />
      <div className="home-container">
        <div className="content-container">
          <div className="single-content-container">
            <h2>Club Schedule</h2>
            <FullCalendar
              plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
              initialView="timeGridWeek"
              events={events.map(event => ({
                id: event.id,
                title: `Event (${event.participants}/20)`,
                start: event.start_date,
                end: event.end_date,
                extendedProps: {
                  participants: event.participants
                }
              }))}
              headerToolbar={{
                left: 'prev,next today',
                center: 'title',
                right: 'dayGridMonth,timeGridWeek,timeGridDay'
              }}
              eventClick={handleEventClick}
              editable={true}
              selectable={true}
              slotMinTime="09:00:00"
              slotMaxTime="24:00:00"
            />

            <Modal
              isOpen={modalIsOpen}
              onRequestClose={() => setModalIsOpen(false)}
              contentLabel="Event Registration"
            >
              <h2>Register for Event</h2>
              <p>{selectedEvent?.title}</p>
              <p>{`Participants: ${selectedEvent?.participants}/20`}</p>
              <button onClick={handleParticipate} disabled={selectedEvent?.participants >= 20}>
                Participate
              </button>
              <button onClick={() => setModalIsOpen(false)}>Close</button>
            </Modal>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default ClubSchedule;